import { useEffect, useRef } from 'react';
import { Form, usePendingFormSubmit, useRouteData, useSubmit } from '@premix/core';
import Button from '../components/Button';
import '../styles/action.css';
import '../styles/style.css';
export default function ActionPage() {
  const {
    todos
  } = useRouteData();
  const pendingFormSubmit = usePendingFormSubmit();
  const formRef = useRef();
  const submit = useSubmit();
  useEffect(() => {
    if (pendingFormSubmit) {
      formRef.current.reset();
    }
  }, [pendingFormSubmit]);
  useEffect(() => {
    console.log(todos);
  }, [todos]);
  return <main>
      <h2>Todos</h2>
      <ul>
        {(todos || []).map(todo => <li key={todo.id}>
            <input type="checkbox" checked={todo.complete} onChange={e => {
          submit({
            id: todo.id,
            complete: e.target.checked
          }, {
            replace: true,
            method: 'put'
          });
        }} />
            {todo.text}
          </li>)}
        {pendingFormSubmit && <li>
            <input type="checkbox" disabled />
            {pendingFormSubmit.get('text')}
          </li>}
      </ul>
      <Form ref={formRef} action="/todos">
        <label>
          Todo:
          <input name="text" type="text" />
        </label>
        <Button type="submit">Create</Button>
      </Form>
    </main>;
}